// common mixins - start

@mixin mkdfBckImageStyle() {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}

@mixin mkdfImageOverlayHoverStyle($with-hover: true) {
    
    @if ($with-hover) {
        
        &:hover {
            
            &:after {
                opacity: 1;
            }
        }

        &:after {
            @include mkdfAbsoluteHolderLayout();
            content: '';
            background-color: rgba($default-heading-color, .4);
            opacity: 0;
            transition: opacity .2s ease-in-out;
        }

    } @else {
        @include mkdfAbsoluteHolderLayout();
        content: '';
        background-color: rgba($default-heading-color, .4);
        opacity: 0;
        transition: opacity .2s ease-in-out;
    }
}

@mixin mkdfStandardPaginationStyle($list_type: null) {
	@include mkdfRelativeHolderLayout(top);
	margin: 40px 0 0;
	clear: both;
	
	ul {
		@include mkdfRelativeHolderLayout(top);
		padding: 0;
		margin: 0;
		list-style: none;
		text-align: center;
		height: 60px;
		
		li {
			display: inline-block;
			vertical-align: top;
			
			a {
				position: relative;
				display: inline-block;
				vertical-align: top;
				margin: 0;
			}
			
			&.mkdf-pag-prev,
			&.mkdf-pag-next {
				
				a {
					display: block;
					position: absolute;
					font-family: $default-heading-font;
					font-size: 14px;
					font-weight: 700;
					letter-spacing: .1em;
					text-transform: uppercase;
					line-height: 60px;
					color: $default-heading-color;
					transition: .3s ease-in-out;
					
					&:before {
						content: '';
						position: absolute;
						display: block;
						width: 79px;
						height: 60px;
						color: inherit;
						border: 1px solid $default-border-color;
						box-sizing: border-box;
						transition: width .3s ease;
					}
					
					&:after {
						content: '';
						position: absolute;
						display: block;
						width: 74px;
						height: 1px;
						top: calc(50% - 1px);
						background-color: currentColor;
						transform: scaleX(1);
						opacity: 1;
						transition: .3s ease-in-out;
					}
					
					.mkdf-nav-label {
						position: relative;
						display: inline-block;
						transition: .3s ease-in-out;
						
						&:before {
							content: '';
							position: absolute;
							display: block;
							width: 74px;
							height: 1px;
							top: calc(50% - 1px);
							background-color: currentColor;
							transition: .3s ease-in-out;
							transform: scaleX(0);
							opacity: .4;
						}
					}
					
					.mkdf-nav-mark {
						position: relative;
						display: inline-block;
						font-size: 13px;
					}
					
					&:hover {
						
						&:before {
							width: calc(100% + 55px);
						}
						
						&:after {
							transform: scaleX(0);
							opacity: .4;
						}
						
						.mkdf-nav-mark {
							&:before {
								transform: scaleX(1);
								opacity: 1;
							}
						}
					}
				}
			}
			
			&.mkdf-pag-prev {
				float: left;
				
				a {
					left: 0;
					
					&:before {
						left: 0;
					}
					
					&:after {
						left: 43px;
						transform-origin: left;
					}
					
					.mkdf-nav-mark {
						margin: 0 0 0 122px;
						right: 87px;
						transform: scaleX(-1);
					}
					
					.mkdf-nav-label {
						&:before{
							left: calc(100% + 20px);
							transform-origin: left;
						}
					}
					
					&:hover {
						.mkdf-nav-label {
							transform: translateX(-74px);
							
							&:before{
								transform: scaleX(1);
								opacity: 1;
							}
						}
					}
				}
			}
			
			&.mkdf-pag-next {
				float: right;
				
				a {
					right: 0;
					
					&:before {
						right: 0;
					}
					
					&:after {
						right: 43px;
						transform-origin: right;
					}
					
					.mkdf-nav-mark {
						margin: 0 0 0 122px;
						right: 35px;
					}
					
					.mkdf-nav-label {
						&:before{
							right: calc(100% + 20px);
							transform-origin: right;
						}
					}
					
					&:hover {
						.mkdf-nav-label {
							transform: translateX(74px);
							
							&:before{
								transform: scaleX(1);
								opacity: 1;
							}
						}
					}
				}
			}
			
			@media (max-width: $phone-portrait) {
				
				&.mkdf-pag-prev,
				&.mkdf-pag-next {
					a {
						.mkdf-nav-label {
							display: none;
						}
						
						&:after {
							width: 50px;
						}
						
						&:hover {
							&:before {
								width: 79px;
							}
						}
					}
				}
			}
			
            @if ($list_type == 'shortcode') {
	            
                &.mkdf-pag-prev {
                    
                    a {
                        opacity: 0;
                    }
                }

                &.mkdf-pag-next {
                    a {
                        opacity: 1;
                    }
                }

            } @else if ($list_type == 'shop') {
	            
	            span {
		            position: relative;
		            display: inline-block;
		            vertical-align: top;
		            line-height: 60px;
		            
		            &.mkdf-woo-nav-label {
			            position: relative;
			            display: inline-block;
			            transition: .3s ease-in-out;
			            
			            &:before{
				            content: '';
				            position: absolute;
				            display: block;
				            width: 74px;
				            height: 1px;
				            top: calc(50% - 1px);
				            background-color: currentColor;
				            transition: .3s ease-in-out;
				            transform: scaleX(0);
				            opacity: .4;
			            }
		            }
		
		            &.mkdf-woo-nav-mark {
			            position: relative;
			            display: inline-block;
			            font-size: 13px;
		            }
		
		            &.dots ,
		            &.current {
			            display: none;
		            }
	            }
	
	            a {
		            line-height: 60px;
		            display: none;
		            
		            &.next,
		            &.prev {
			            display: block;
			            position: absolute;
			            font-family: $default-heading-font;
			            font-size: 14px;
			            font-weight: 700;
			            letter-spacing: .1em;
			            text-transform: uppercase;
			            color: $default-heading-color;
			            transition: .3s ease-in-out;
			
			            &:before {
				            content: '';
				            position: absolute;
				            display: block;
				            width: 79px;
				            height: 60px;
				            color: inherit;
				            border: 1px solid $default-border-color;
				            box-sizing: border-box;
				            transition: width .3s ease;
			            }
			
			            &:after {
				            content: '';
				            position: absolute;
				            display: block;
				            width: 74px;
				            height: 1px;
				            top: calc(50% - 1px);
				            background-color: currentColor;
				            transform: scaleX(1);
				            opacity: 1;
				            transition: .3s ease-in-out;
			            }
			
			            &:hover {
				
				            &:before {
					            width: calc(100% + 55px);
				            }
				
				            &:after {
					            transform: scaleX(0);
					            opacity: .4;
				            }
				
				            span.mkdf-woo-nav-label {
					            &:before {
						            transform: scaleX(1);
						            opacity: 1;
					            }
				            }
			            }
		            }
		
		            &.prev {
			            left: 0;
			
			            .mkdf-woo-nav-mark {
				            margin: 0 122px 0 0;
				            left: 35px;
				            transform: scaleX(-1);
			            }
			
			            .mkdf-woo-nav-label {
				           &:before{
					           left: calc(100% + 20px);
					           transform-origin: left;
				           }
			            }
			
			            &:before {
				            left: 0;
			            }
			
			            &:after {
				            left: 43px;
				            transform-origin: left;
			            }
			
			            &:hover {
				
				            .mkdf-woo-nav-label {
					            transform: translateX(-74px);
				            }
			            }
		            }
		
		            &.next {
			            right: 0;
			
			            .mkdf-woo-nav-mark {
				            margin: 0 0 0 122px;
				            right: 35px;
			            }
			
			            .mkdf-woo-nav-label {
				            &:before{
					            right: calc(100% + 20px);
					            transform-origin: right;
				            }
			            }
			
			            &:before {
				            right: 0;
			            }
			
			            &:after {
				            right: 43px;
				            transform-origin: right;
			            }
			
			            &:hover {
				            .mkdf-woo-nav-label {
					            transform: translateX(74px);
				            }
			            }
		            }
	            }
	
	            @media (max-width: $phone-portrait) {
		            a {
			            &.next,
			            &.prev {
				            &:hover {
					            &:before {
						            width: 79px;
					            }
				            }
				            &:after {
					            width: 50px;
				            }
			            }
		            }
		            
		            span {
			            &.mkdf-woo-nav-label {
				            display: none;
			            }
		            }
	            }
            }
        }
    }
}

@mixin mkdfButtonDefaultStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    width: auto;
    margin: 0;
    font-family: $default-heading-font;
	font-size: 14px;
	letter-spacing: 0.1em;
	line-height: 2em;
    font-weight: 700;
    text-transform: uppercase;
    outline: none;
    box-sizing: border-box;
    transition: color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out;
}

@mixin mkdfButtonSize($size: medium) {
    
    @if ($size == 'small') {
        padding: 12px 13px;

    } @else if ($size == 'medium') {
        padding: 12px 23px;

    } @else if ($size == 'large') {
        padding: 15px 43px;

    } @else if ($size == 'huge') {
        display: block;
        width: 100%;
        text-align: center;
        padding: 15px 43px;
    }
}

@mixin mkdfButtonPredefined($type: null, $width: '33px') {
	box-sizing: border-box;
	color: #fff;
	background-color: $first-main-color;
	border: 1px solid $first-main-color;
	border-radius: $default-border-radius;
	
	.mkdf-btn-predefined-line-holder {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: flex-start;
		overflow: hidden;
		
		.mkdf-ms-explorer &{
			display: block;
		}
		
		> i,
		> span:not(.mkdf-btn-text) {
			display: inline-block;
			vertical-align: middle;
			&:not(.mkdf-ms-explorer) &{
				line-height: 1;
			}
			
			&:before {
				display: block;
				line-height: inherit;
			}
		}
		
		.ion-ios-play {
			font-size: 13px;
			width: 6px;
			padding-top: 1px;
			
			.mkdf-ms-explorer &{
				margin-left: -4px;
			}
		}
		
		.mkdf-btn-text {
			position: relative;
			display: inline-block;
			transition: .2s cubic-bezier(0.42, 0, 0.6, 0.93);
			transform: translateX(0);
			padding: 0 18px 0 21px;
		}
		
		.mkdf-btn-line-hidden,
		.mkdf-btn-line {
			width: calc(#{$width} - 7px);
			height: 1px;
			background-color: currentColor;
			transition: .18s ease-in-out;
			
			.mkdf-edge &
			{
				max-height: 1px;
				overflow: hidden;
			}
		}
		
		.mkdf-btn-line-hidden {
			position: absolute;
			top: 50%;
			opacity: .4;
			transform-origin: left;
			transform: scaleX(0);
		}
		
		.mkdf-btn-line {
			opacity: 1;
			transform: scaleX(1);
			transform-origin: right;
		}
    }

    &:hover {

        .mkdf-btn-text {
	        transform: translateX(calc(#{$width} - 8px));
	        
	        .mkdf-ms-explorer & {
		        -ms-transform: translateX(#{$width}) translateX(-8px);
	        }
        }

        .mkdf-btn-line {
	        opacity: .4;
	        transform: scaleX(0);
	        transform-origin: right;
        }
	
	    .mkdf-btn-line-hidden {
		    transform: scaleX(1);
		    opacity: 1;
	    }
    }

    @if ( $type == 'simple' ){
        padding: 0 !important;
        background-color: transparent;
        color: $default-heading-color;
        border: none;
	
	    &:hover {
		    color: $default-heading-color;
		    background-color: transparent;
		
		    .mkdf-btn-predefined-line-holder {
			    
			    .mkdf-btn-text {
				    transition: .3s ease-in-out;
			    }
		    }
	    }
	
	    .mkdf-btn-predefined-line-holder {
		    
		    .mkdf-btn-text {
			    padding: 0 18px 0 0;
			    transition: .32s cubic-bezier(0.42, 0, 0.6, 0.93);
		    }
		
		    .mkdf-btn-line-hidden,
		    .mkdf-btn-line {
			    transition: .3s ease-in-out;
		    }
		
		    .mkdf-btn-line-hidden {
			    width: calc(#{$width} - 27px);
		    }
	    }
    }
}


