@mixin mkdfTagLineStyle($size: normal) {
	font-family: $default-tagline-font;
	line-height: 1.1em;
	font-weight: 500;
	text-transform: uppercase;
	color: $first-main-color;
	
	@if ($size == 'small') {
		font-size: 11px;
		letter-spacing: .075em;
	} @else if ($size == 'normal') {
		font-size: 12px;
		letter-spacing: .8em;
	} @else if ($size == 'large') {
		font-size: 14px;
		letter-spacing: .8em;
	}
}
@mixin mkdfCustomMarkStyle($font-size: 140px) {
	position: absolute;
	display: block;
	font-family: $default-heading-font;
	font-size: $font-size;
	line-height: 1;
	letter-spacing: -.05em;
	font-weight: 700;
	color: rgba($default-background-color, .15);
}

@mixin mkdfHeadingStyle($heading: null, $with-color: true, $with-link-style: false) {
	font-family: $default-heading-font;
	font-weight: 700;
	letter-spacing: .025em;
	text-transform: uppercase;
	
	@if ($with-color) {
		color: $default-heading-color;
	}
	
	@if ($heading == 'h1') {
		@include mkdfH1();
	} @else if ($heading == 'h2') {
		@include mkdfH2();
	} @else if ($heading == 'h3') {
		@include mkdfH3();
	} @else if ($heading == 'h4') {
		@include mkdfH4();
	} @else if ($heading == 'h5') {
		@include mkdfH5();
	} @else if ($heading == 'h6') {
		@include mkdfH6();
	}
	
	@if ($with-link-style) {
		
		a {
			@include mkdfTypographyLayout();
			
			&:hover {
				color: $first-main-color;
			}
		}
	}
}

@mixin mkdfDefaultHeadingStyle() {
    @include mkdfHeadingStyle();
    margin: 25px 0;

    -ms-word-wrap: break-word;
    word-wrap: break-word;

    a {
        @include mkdfTypographyLayout();

        &:hover {
            color: $first-main-color;
        }
    }
}

@mixin mkdfBody() {
    font-family: $default-text-font;
    font-size: 16px;
    font-weight: 300;
    line-height: 27px;
    color: $default-text-color;
    background-color: $default-background-color;
    -webkit-font-smoothing: antialiased;
}

// heading mixins - end

